import { motion } from "framer-motion";
import React from "react";
import "./style.css";

export default function TransitionTypes() {
    return (
        <div className="section">
            <motion.div
                className="box"
                animate={{
                    y: 100,
                }}
                transition={{
                    ease: "circInOut",
                    duration: 2,
                    delay: 0.5,
                    type: "spring", // üëâ ÂΩàÁ∞ß
                    damping: 5, // üëâ Ê∏õÈúá
                }}
            >
                spring
            </motion.div>

            <motion.div
                className="box"
                animate={{
                    y: 100,
                }}
                transition={{
                    ease: "backOut",
                    duration: 2,
                    delay: 0.5,
                    type: "tween", // üëâ Ë£úÈñìÂãïÁï´
                }}
            >
                tween
            </motion.div>
            <motion.div
                className="box"
                animate={{
                    y: 100,
                }}
                transition={{
                    ease: "linear",
                    duration: 2,
                    delay: 0.5,
                    type: "inertia", // üëâ ÊÖ£ÊÄßÂãïÁï´
                    velocity: 200, // üëâ ÈÄüÂ∫¶
                    power: 0.5, // üëâ ÂäüÁéáÔºåË∂äÂ§ßË∂äÁî®Âäõ
                }}
            >
                inertia
            </motion.div>
        </div>
    );
}
